"use strict";



var paginator =  {


	/**
	* BUILD_PAGE_NODES
	* Build an array ob ojects ready to draw a footer pagination
	*/
	build_page_nodes : function(total, limit, offset) {

		const ar_nodes = []

		// prev
			if (offset>limit) {
				ar_nodes.push({
					label 		 : tstring.prev || "Prev",
					offset_value : 0,
					type 		 : "navigator"
				})
			}

		// intermediate
			const n_pages = ceil(total / limit)
			for (var i = 0; i < n_pages; i++) {

				// skip first an last pages
					if (i===0 || i===n_pages) continue
				
				ar_nodes.push({
					label 		 : i,
					offset_value : i * limit,
					type 		 : "page"
				})
			}


		// next
			if (offset < (total-limit)) {
				ar_nodes.push({
					label 		 : tstring.next || "Next",
					offset_value : offset + limit,
					type 		 : "navigator"
				})
			}

		// debug
			if(SHOW_DEBUG===true) {
				console.log("ar_nodes:",ar_nodes);;
			}

		return ar_nodes
	},//end build_page_nodes


}//end paginator